import { useState, useEffect } from "react";
import "./randomChar.scss";
import Spiner from "../spiner/Spiner";
import ErrorMessage from "../errorMessage/ErrorMessage";
import useMarvelService from "../../services/useMarvelService";
import mjolnir from "../../resources/img/mjolnir.png";

const RandomChar = () => {
  const [char, setChar] = useState({});
  const { loading, error, getCharacter, clearError } = useMarvelService();

  useEffect(() => {
    updateCharacter();
  }, []);

  const onCharLoaded = (char) => {
    setChar(char);
  };

  const updateCharacter = () => {
    clearError();
    const id = Math.floor(Math.random() * (1011400 - 1011000) + 1011000);
    getCharacter(id).then(onCharLoaded);
  };

  const errorMessage = error ? <ErrorMessage /> : null;
  const spinner = loading ? <Spiner /> : null;
  const content = !(error || loading) ? <Viev char={char} /> : null;

  return (
    <div className="randomchar">
      {errorMessage}
      {spinner}
      {content}
      <div className="randomchar__static">
        <p className="randomchar__title">
          Random character for today!
          <br />
          Do you want to get to know him better?
        </p>
        <p className="randomchar__title">Or choose another one</p>
        <button className="button button__main">
          <div className="inner" onClick={updateCharacter}>
            try it
          </div>
        </button>
        <img src={mjolnir} alt="mjolnir" className="randomchar__decoration" />
      </div>
    </div>
  );
};
const Viev = ({ char }) => {
  const { name, description, thumbnail, homepage, wiki } = char;
  const chageDescription = (str) => {
    if (!str) {
      return "There is no information about this hero";
    }
    if (str.length > 221) {
      let newStr = str.slice(0, 217) + "...";
      return newStr;
    } else {
      return str;
    }
  };

  const changePic = (pic) => {
    if (
      pic ===
      "http://i.annihil.us/u/prod/marvel/i/mg/b/40/image_not_available.jpg"
    ) {
      return (
        <img
          src={pic}
          alt="Random character"
          className="randomchar__img"
          style={{ objectFit: "contain" }}
        />
      );
    } else {
      return (
        <img src={pic} alt="Random character" className="randomchar__img" />
      );
    }
  };

  return (
    <div className="randomchar__block">
      {changePic(thumbnail)}
      <div className="randomchar__info">
        <p className="randomchar__name">{name}</p>
        <p className="randomchar__descr">{chageDescription(description)}</p>
        <div className="randomchar__btns">
          <a href={homepage} className="button button__main">
            <div className="inner">homepage</div>
          </a>
          <a href={wiki} className="button button__secondary">
            <div className="inner">Wiki</div>
          </a>
        </div>
      </div>
    </div>
  );
};

export default RandomChar;
